
.TodoList {padding: 0;}
.TodoList li {background: rgb(240, 242, 245); padding: 15px 30px; color: rgb(0, 0, 0); 
                font-weight: bold; list-style: none; font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; }
.TodoList li span {margin-right: 25px; cursor: pointer;}
.TodoList li.on span {color: rgb(199, 197, 197);}
.TodoList li em {cursor: pointer;}
.TodoList li.on em {color: rgb(199, 197, 197); text-decoration: line-through rgb(159, 127, 231);}
.TodoList li button {float: right; border: none; color: rgb(148, 181, 230); }